<template>
	<view>

		<view class="address-choosebox" v-if="JSON.stringify(address) ==='{}'">
			<button type="primary" size="mini" class="btnchoose" @click="chooseAddress">请选择收货地址+</button>
		</view>

		<view class="address-info" v-else @click="chooseAddress">
			<view class="row1">
				<view class="rleft">
					<view class="uname">
						姓名：<text>{{address.userName}}</text>
					</view>
				</view>
				<view class="rright">
					<view class="uphone">
						电话：<text>{{address.telNumber}}</text>
						<uni-icons type="arrowright" size="16"></uni-icons>
					</view>
				</view>
			</view>

			<view class="row2">
				<view class="r2l">
					收货地址：
				</view>
				<view class="r2r">
					{{addstr}}
				</view>
			</view>
		</view>

		<image src="/static/cart_border@2x.png" class="address-border"></image>

	</view>
</template>

<script>
	import {
		mapState,
		mapMutations,
		mapGetters} from 'vuex'
	export default {
		name: "my-address",
		data() {
			return {
				// address:{},
			};
		},
		methods: {
			...mapMutations('m_user', ['updateAddress']),
			async chooseAddress() {
				const [err, succ] = await uni.chooseAddress().catch(err => err)
				// console.log([err, succ]);
				if (err === null && succ.errMsg === 'chooseAddress:ok')
					// this.address = succ
					// console.log(this.address);
					this.updateAddress(succ)
					
				if(err && (err.errMsg === 'chooseAddress:fail auth deny' || err.errMsg === 'chooseAddress:fail authorize no response')){
					this.reAuth()
				}
			},
			async reAuth() {
			  // 3.1 提示用户对地址进行授权
			  const [err2, confirmResult] = await uni.showModal({
			    content: '检测到您没打开地址权限，是否去设置打开？',
			    confirmText: "确认",
			    cancelText: "取消",
			  })
			
			  // 3.2 如果弹框异常，则直接退出
			  if (err2) return
			
			  // 3.3 如果用户点击了 “取消” 按钮，则提示用户 “您取消了地址授权！”
			  if (confirmResult.cancel) return uni.$showMsg('您取消了地址授权！')
			
			  // 3.4 如果用户点击了 “确认” 按钮，则调用 uni.openSetting() 方法进入授权页面，让用户重新进行授权
			  if (confirmResult.confirm) return uni.openSetting({
			    // 3.4.1 授权结束，需要对授权的结果做进一步判断
			    success: (settingResult) => {
			      // 3.4.2 地址授权的值等于 true，提示用户 “授权成功”
			      if (settingResult.authSetting['scope.address']) return uni.$showMsg('授权成功！请选择地址')
			      // 3.4.3 地址授权的值等于 false，提示用户 “您取消了地址授权”
			      if (!settingResult.authSetting['scope.address']) return uni.$showMsg('您取消了地址授权！')
			    }
			  })
			}
			
		},
		computed: {
			...mapState('m_user', ['address']),
			...mapGetters('m_user', ['addstr']),
		}
	}
</script>

<style lang="scss">
	.address-border {
		display: block;
		width: 100%;
		height: 5px;
	}

	.address-choosebox {
		height: 90px;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.address-info {
		font-size: 14px;
		display: flex;
		height: 90px;
		flex-direction: column;
		justify-content: center;
		padding: 0 5px;

		.row1 {
			display: flex;
			justify-content: space-between;

			.rleft {
				display: flex;
				align-items: center;
			}

			.rright {
				.uphone {
					padding-right: 5px;
				}
			}

		}

		.row2 {
			display: flex;
			margin-top: 10px;
			align-items: center;

			.r2l {
				white-space: nowrap;
			}
		}
	}
</style>
